<template>
  <div>
    <fTitle>
      <template #title>DateTimePicker 日期时间选择器</template>
      <template #introduce>在同一个选择器里选择日期和时间</template>
    </fTitle>
    <sTitle>
      <template #title>各种基础用法</template>
      <template #introduce></template>
    </sTitle>
    <div class="outbox">
      <div class="inbox">
        <el-row type="flex" justify="space-around">
          <el-col :span="8"> <div class="grid-content">
           <h3>默认</h3>
           <el-date-picker
      v-model="value1"
      type="datetime"
      placeholder="选择日期时间">
    </el-date-picker>
          </div></el-col>
          <el-col :span="8"> <div class="grid-content">
            <h3>带快捷选项</h3>
            <el-date-picker
      v-model="value2"
      type="datetime"
      placeholder="选择日期时间"
      align="right"
      :picker-options="pickerOptions">
    </el-date-picker>
          </div></el-col>
          <el-col :span="8"> <div class="grid-content">
            <h3>设置默认时间</h3>
            <el-date-picker
      v-model="value3"
      type="datetime"
      placeholder="选择日期时间"
      default-time="12:00:00">
    </el-date-picker>
          </div></el-col>
        </el-row>
      </div></div>

      <sTitle>
      <template #title>默认的起始与结束时刻</template>
      <template #introduce></template>
    </sTitle>
    <div class="outbox">
      <div class="inbox">
        <el-row type="flex" justify="space-around">
          <el-col :span="12"> <div class="grid-content">
           <h3>默认</h3>
           <el-date-picker
      v-model="value3"
      type="datetimerange"
      range-separator="至"
      start-placeholder="开始日期"
      end-placeholder="结束日期">
    </el-date-picker>
          </div></el-col>
          <el-col :span="12"> <div class="grid-content">
            <h3>带快捷选项</h3>
            <el-date-picker
      v-model="value4"
      type="datetimerange"
      :picker-options="pickerOptions2"
      range-separator="至"
      start-placeholder="开始日期"
      end-placeholder="结束日期"
      align="right">
    </el-date-picker>
          </div></el-col>
        </el-row>
      </div></div>
  </div>

</template>

<script>
export default {
  name: 'ColorIndex',
  data () {
    return {
      pickerOptions2: {
        shortcuts: [{
          text: '最近一周',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
            picker.$emit('pick', [start, end])
          }
        }, {
          text: '最近一个月',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
            picker.$emit('pick', [start, end])
          }
        }, {
          text: '最近三个月',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
            picker.$emit('pick', [start, end])
          }
        }]
      },
      pickerOptions: {
        shortcuts: [{
          text: '今天',
          onClick (picker) {
            picker.$emit('pick', new Date())
          }
        }, {
          text: '昨天',
          onClick (picker) {
            const date = new Date()
            date.setTime(date.getTime() - 3600 * 1000 * 24)
            picker.$emit('pick', date)
          }
        }, {
          text: '一周前',
          onClick (picker) {
            const date = new Date()
            date.setTime(date.getTime() - 3600 * 1000 * 24 * 7)
            picker.$emit('pick', date)
          }
        }]
      },
      value1: '',
      value2: '',
      value3: [new Date(2000, 10, 10, 10, 10), new Date(2000, 10, 11, 10, 10)],
      value4: '',
      value5: '',
      value6: ''

    }
  }
}
</script>

<style lang="less" scoped>
@import '../../styles/row.less';
.outbox{
    width: 100%;
    height: auto;
    background-color: #ffffff;
    display: flex;
    align-items: center;
    flex-direction:row;
    justify-content: center;
    .inbox{
      width: 85%;
      height: auto;
      background-color: #ffffff;
      border: 1px solid darkgray;
      .grid-content{
        height: auto;
        background: white;
        margin-bottom: 20px;
        align-items: center;
        }
    }}
</style>
